import { ProductInfo } from "@/dto/ProductInfo";
import { FirestoreService } from "@/services/FirestoreService";
import { Component, Vue, Watch } from "vue-property-decorator";

import VueSilentbox from "vue-silentbox";

import SellerDetails from "@/dto/SellerDetails";
import { CartService } from "@/services/CartService";
import { SubCategoryInfo } from "@/dto/SubCategoryInfo";

@Component({
  components: {
    VueSilentbox
  }
})
export default class Categories extends Vue {
  registerError = "";
  showError = false;
  email = "";
  password = "";
  repeatedPassword = "";
  loading = false;
  rating = 5;
  detailsLoading = true;
  categoryInfoList: SubCategoryInfo[] = [];
  otherSellersList: SellerDetails[] = [];
  productDesc = "";


  $silentbox: any;

  async mounted() {
    await this.fetchData();
  }
  private async fetchData() {
    try {
      const data = await FirestoreService.getDocuments(
        `parentCategories/${this.$route.params.id}/subCategories`,
        SubCategoryInfo.converter
      );
      if (!data.empty) {
        data.forEach((doc: any) => {
          this.categoryInfoList.push(doc.data());
        });
      }

      this.detailsLoading = false;
      console.log(this.categoryInfoList);
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "cross", type: "error" });
    }
  }

  openOverlay(image: any, index: number) {
    this.$silentbox.open(image);
  }
  @Watch("$route", { deep: true })
  async onRouteChanged(value: boolean, oldValue: boolean) {
    await this.fetchData();
  }

}
